@use 'var';
@use 'hero';
@use 'shows';

@font-face {
    font-family: Avenir;
    src: url(/build/fonts/Avenir-Regular.woff2);
    font-weight: 400;
}

@font-face {
    font-family: Avenir;
    src: url(/build/fonts/Avenir-Bold.woff2);
    font-weight: 700;
}



*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Avenir, sans-serif;
}


body {
    background-color: var.$corDeFundo;
    
}

@mixin text($fontSize: 16px) {
    font-size: $fontSize;
    line-height: $fontSize + 10px;
    color: var.$corTextoPrincipal;
}
@mixin title($fontSize: 28px) {
    @include text($fontSize);
    color: var.$corTextoSecundario;
}

.text--big {
    @include text(18px);

}
.text {
    @include text;
}

.text--small {
    @include text(12px);
}
.title--big {
    @include title(44px);
}

.title {
    @include title(40px);
}

.title--small {
    @include title(28px);
}

.link-text {
    @include text(18px);
    color: var.$corTextoSecundario;
    text-decoration: underline;
}