@use 'var';

.shows {
    padding: 2vw 5.6vw;
    &__list {
        margin-top: 44px;
        display: none;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 24px;
        
        &--is-active {
            display: grid;
        }

        &__item {
            img {
                max-width: 100%;
                border-radius: 4px;

            }
        }

    }
    &__tabs {
        display: flex;
        justify-content: center;
        &__button {
            color: var.$corTextoSecundario;
            font-size: 20px;
            border: none;
            padding-bottom: 5px;
            background: none;
            text-transform: uppercase;
            margin: 0 1vw;
            cursor: pointer;
            border-bottom: 5px solid transparent;
            
            &--is-active {
                border-color: var.$corTextoSecundario;

            }
            
        }
    }
}